"use client";

import { useState, useEffect, useCallback, useMemo } from 'react';
import { useRouter } from 'next/navigation';
import { Search, RefreshCw, BarChart3, LineChart } from 'lucide-react';
import { DashboardLayout } from '@/components/layout/DashboardLayout';
import { ProjectTable, ProjectTableSkeleton } from '@/components/projects/ProjectTable';
import { AddProjectDialog } from '@/components/projects/AddProjectDialog';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { ResponsiveContainer, BarChart, Bar, XAxis, YAxis, Tooltip, CartesianGrid, LineChart as ReLineChart, Line } from 'recharts';

export default function ProjectsPage() {
  const router = useRouter();
  const [projects, setProjects] = useState([]);
  const [lookups, setLookups] = useState({ depts: [], cats: [] });
  const [loading, setLoading] = useState(true);

  // Filters
  const [filters, setFilters] = useState({ search: '', dept: 'all', cat: 'all', status: 'all', year: 'all' });

  const fetchProjects = useCallback(async () => {
    setLoading(true);
    const params = new URLSearchParams({
      search: filters.search,
      departmentId: filters.dept,
      categoryId: filters.cat,
      status: filters.status,
      year: filters.year
    });
    const res = await fetch(`/api/projects?${params.toString()}`);
    setProjects(await res.json());
    setLoading(false);
  }, [filters]);

  useEffect(() => {
    Promise.all([fetch('/api/departments'), fetch('/api/categories')])
      .then(async ([d, c]) => setLookups({ depts: await d.json(), cats: await c.json() }));
    fetchProjects();
  }, [filters, fetchProjects]);

  // Analytics Data for Charts
  const chartData = useMemo(() => {
    const timeSeries = projects.reduce((acc: any[], p: any) => {
      const date = new Date(p.startDate).toLocaleDateString('en-US', { month: 'short' });
      const existing = acc.find(i => i.name === date);
      if (existing) existing.count += 1;
      else acc.push({ name: date, count: 1 });
      return acc;
    }, []).slice(-6);

    const stacked = projects.slice(0, 10).map((p: any) => {
      const received = p.bills.reduce((s: number, b: any) => s + Number(b.receivedAmount || 0), 0);
      return {
        name: p.projectName.substring(0, 10) + '...',
        received: received,
        due: Number(p.totalProjectValue) - received
      };
    });

    return { timeSeries, stacked };
  }, [projects]);

  return (
    <DashboardLayout title="Projects" currentPath="/projects" onNavigate={(path) => router.push(path)}>
      <div className="space-y-6">
        <div className="flex justify-between items-center">
          <h1 className="text-2xl font-bold">Project Management</h1>
          <div className="flex gap-2">
            <Button variant="outline" size="icon" onClick={fetchProjects} disabled={loading}>
              <RefreshCw className={loading ? "animate-spin" : ""} size={16} />
            </Button>
            <AddProjectDialog onProjectAdded={fetchProjects} />
          </div>
        </div>

        {/* Analytics Section */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-4 h-64">
          <div className="glass-card p-4 rounded-xl">
            <p className="text-xs font-bold uppercase mb-4 flex items-center gap-2"><LineChart size={14}/> Onboarding Trend</p>
            <ResponsiveContainer width="100%" height="80%">
              <ReLineChart data={chartData.timeSeries}>
                <CartesianGrid strokeDasharray="3 3" vertical={false} />
                <XAxis dataKey="name" fontSize={10} />
                <YAxis fontSize={10} />
                <Tooltip />
                <Line type="monotone" dataKey="count" stroke="hsl(var(--primary))" strokeWidth={2} />
              </ReLineChart>
            </ResponsiveContainer>
          </div>
          <div className="glass-card p-4 rounded-xl">
            <p className="text-xs font-bold uppercase mb-4 flex items-center gap-2"><BarChart3 size={14}/> Budget Realization</p>
            <ResponsiveContainer width="100%" height="80%">
              <BarChart data={chartData.stacked}>
                <XAxis dataKey="name" fontSize={10} />
                <YAxis fontSize={10} />
                <Tooltip />
                <Bar dataKey="received" stackId="a" fill="#22c55e" radius={[0, 0, 0, 0]} />
                <Bar dataKey="due" stackId="a" fill="#ef4444" radius={[4, 4, 0, 0]} />
              </BarChart>
            </ResponsiveContainer>
          </div>
        </div>

        {/* Filter Bar */}
        <div className="flex flex-wrap gap-2 p-3 glass-card rounded-xl">
          <Input placeholder="Search..." className="w-64" onChange={(e) => setFilters({...filters, search: e.target.value})} />
          <Select onValueChange={(v) => setFilters({...filters, dept: v})}>
            <SelectTrigger className="w-40"><SelectValue placeholder="Dept" /></SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Depts</SelectItem>
              {lookups.depts.map((d: any) => <SelectItem key={d.id} value={d.id.toString()}>{d.name}</SelectItem>)}
            </SelectContent>
          </Select>
          <Select onValueChange={(v) => setFilters({...filters, cat: v})}>
            <SelectTrigger className="w-40"><SelectValue placeholder="Category" /></SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Categories</SelectItem>
              {lookups.cats.map((c: any) => <SelectItem key={c.id} value={c.id.toString()}>{c.name}</SelectItem>)}
            </SelectContent>
          </Select>
        </div>

        {loading ? <ProjectTableSkeleton /> : <ProjectTable projects={projects} onProjectClick={(p) => router.push(`/projects/${p.id}`)} />}
      </div>
    </DashboardLayout>
  );
}