"use client";

import { useState, useEffect, useMemo } from "react";
import { useForm, useFieldArray } from "react-hook-form";
import { Plus, Trash2, Loader2, Building2, AlertTriangle, CheckCircle2 } from "lucide-react";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Checkbox } from "@/components/ui/checkbox";
import { toast } from "sonner";
import { cn } from "@/lib/utils";

export function AddProjectDialog({ onProjectAdded }: { onProjectAdded: () => void }) {
  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(false);
  const [clients, setClients] = useState([]);
  const [categories, setCategories] = useState([]);
  const [departments, setDepartments] = useState([]);
  const [isNewClient, setIsNewClient] = useState(false);
  const [ignoreTotalCheck, setIgnoreTotalCheck] = useState(false);

  const { register, control, handleSubmit, reset, watch, setValue, formState: { errors } } = useForm({
    defaultValues: {
      projectName: "",
      clientId: "",
      categoryId: "", // Added category
      newClient: { name: "", contactPerson: "", contactEmail: "", contactPhone: "" },
      departmentId: "",
      totalProjectValue: "",
      startDate: "",
      bills: [
        { billName: "Inception", billPercent: "20", billAmount: "" },
        { billName: "Final handover", billPercent: "80", billAmount: "" }
      ]
    }
  });

  const { fields, append, remove, insert } = useFieldArray({ control, name: "bills" });
  
  const watchedBills = watch("bills");
  const totalValue = parseFloat(watch("totalProjectValue") || "0");

  // Calculate total percentage for validation
  const totalPercentage = useMemo(() => {
    return watchedBills.reduce((sum, bill) => sum + parseFloat(bill.billPercent || "0"), 0);
  }, [watchedBills]);

  const isPercentValid = ignoreTotalCheck || Math.abs(totalPercentage - 100) < 0.01;

  useEffect(() => {
    if (open) {
      Promise.all([
        fetch('/api/clients').then(res => res.json()),
        fetch('/api/categories').then(res => res.json()),
        fetch('/api/departments').then(res => res.json())
      ]).then(([cl, cat, dept]) => {
        setClients(cl);
        setCategories(cat);
        setDepartments(dept);
      });
    }
  }, [open]);

  // Handler for adding standardized deliverables
  const addDeliverable = () => {
    const deliverableCount = watchedBills.filter(b => b.billName.includes("Deliverable")).length;
    // Insert before "Final handover" if it exists
    const finalIndex = watchedBills.findIndex(b => b.billName.toLowerCase().includes("final"));
    const indexToInsert = finalIndex !== -1 ? finalIndex : watchedBills.length;
    
    insert(indexToInsert, { 
      billName: `Deliverable-${deliverableCount + 1}`, 
      billPercent: "0", 
      billAmount: "0" 
    });
  };

  // Synchronize Percent -> Amount
  const handlePercentChange = (index: number, percent: string) => {
    const pct = parseFloat(percent || "0");
    if (totalValue > 0) {
      const amount = (pct / 100) * totalValue;
      setValue(`bills.${index}.billAmount`, amount.toFixed(2));
    }
  };

  // Synchronize Amount -> Percent
  const handleAmountChange = (index: number, amount: string) => {
    const amt = parseFloat(amount || "0");
    if (totalValue > 0) {
      const percent = (amt / totalValue) * 100;
      setValue(`bills.${index}.billPercent`, percent.toFixed(2));
    }
  };

  const onSubmit = async (data: any) => {
    if (!isPercentValid) {
      toast.error(`Total percentage is ${totalPercentage}%. It must be 100% or check 'Ignore total check'.`);
      return;
    }

    setLoading(true);
    try {
      const res = await fetch('/api/projects', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data)
      });
      if (!res.ok) throw new Error("Failed to save");
      toast.success("Project created successfully");
      setOpen(false);
      reset();
      onProjectAdded();
    } catch (err) {
      toast.error("Error creating project");
    } finally {
      setLoading(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>
        <Button className="gap-2"><Plus className="w-4 h-4" /> Add New Project</Button>
      </DialogTrigger>
      <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>Create New Project</DialogTitle>
        </DialogHeader>
        
        <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
          {/* General Info */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label>Project Name</Label>
              <Input {...register("projectName")} placeholder="Enter project name" required />
            </div>
            <div className="space-y-2">
              <Label>Total Value (BDT)</Label>
              <Input 
                type="number" 
                {...register("totalProjectValue")} 
                placeholder="0.00" 
                required 
                onChange={(e) => {
                  const val = parseFloat(e.target.value || "0");
                  // Recalculate all amounts if total value changes
                  watchedBills.forEach((bill, i) => {
                    const amt = (parseFloat(bill.billPercent || "0") / 100) * val;
                    setValue(`bills.${i}.billAmount`, amt.toFixed(2));
                  });
                }}
              />
            </div>
          </div>

          {/* Selectors: Client, Category, Department */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div className="space-y-2">
              <Label>Client</Label>
              <Select onValueChange={(v) => {
                if(v === "new") setIsNewClient(true);
                else { setIsNewClient(false); setValue("clientId", v); }
              }}>
                <SelectTrigger><SelectValue placeholder="Select Client" /></SelectTrigger>
                <SelectContent>
                  <SelectItem value="new" className="text-primary font-bold">+ Add New Client</SelectItem>
                  {clients.map((c: any) => <SelectItem key={c.id} value={c.id.toString()}>{c.name}</SelectItem>)}
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <Label>Category</Label>
              <Select onValueChange={(v) => setValue("categoryId", v)}>
                <SelectTrigger><SelectValue placeholder="Select Category" /></SelectTrigger>
                <SelectContent>
                  {categories.map((c: any) => <SelectItem key={c.id} value={c.id.toString()}>{c.name}</SelectItem>)}
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <Label>Department</Label>
              <Select onValueChange={(v) => setValue("departmentId", v)}>
                <SelectTrigger><SelectValue placeholder="Department" /></SelectTrigger>
                <SelectContent>
                  {departments.map((d: any) => <SelectItem key={d.id} value={d.id.toString()}>{d.name}</SelectItem>)}
                </SelectContent>
              </Select>
            </div>
          </div>

          {/* New Client Form */}
          {isNewClient && (
            <div className="p-4 bg-primary/5 border border-primary/20 rounded-lg space-y-4 animate-in fade-in slide-in-from-top-1">
              <p className="text-sm font-bold flex items-center gap-2 text-primary"><Building2 className="w-4 h-4" /> New Client Details</p>
              <div className="grid grid-cols-2 gap-4">
                <Input placeholder="Client Name" {...register("newClient.name")} required />
                <Input placeholder="Contact Person" {...register("newClient.contactPerson")} />
                <Input placeholder="Email" type="email" {...register("newClient.contactEmail")} />
                <Input placeholder="Phone" {...register("newClient.contactPhone")} />
              </div>
            </div>
          )}

          {/* Billing Phases Section */}
          <div className="space-y-4 border-t pt-4">
            <div className="flex justify-between items-end">
              <div className="space-y-1">
                <Label className="text-lg font-bold">Billing Phases</Label>
                <div className="flex items-center space-x-2">
                  <Checkbox 
                    id="ignoreTotal" 
                    checked={ignoreTotalCheck} 
                    onCheckedChange={(checked) => setIgnoreTotalCheck(!!checked)} 
                  />
                  <label htmlFor="ignoreTotal" className="text-xs text-muted-foreground cursor-pointer">
                    Ignore 100% total check
                  </label>
                </div>
              </div>
              <Button type="button" variant="outline" size="sm" onClick={addDeliverable}>
                <Plus className="w-4 h-4 mr-2" /> Add Deliverable
              </Button>
            </div>
            
            <div className="space-y-3">
              {fields.map((field, index) => (
                <div key={field.id} className="grid grid-cols-12 gap-3 items-end bg-secondary/30 p-3 rounded-lg border border-border/50">
                  <div className="col-span-5 space-y-1">
                    <Label className="text-[10px] uppercase font-bold text-muted-foreground">Phase Name</Label>
                    <Input {...register(`bills.${index}.billName` as const)} placeholder="Phase Name" required />
                  </div>
                  <div className="col-span-2 space-y-1">
                    <Label className="text-[10px] uppercase font-bold text-muted-foreground">Percent (%)</Label>
                    <Input 
                      type="number" 
                      step="0.01"
                      min="0"
                      max="100"
                      {...register(`bills.${index}.billPercent` as const)} 
                      onChange={(e) => handlePercentChange(index, e.target.value)}
                    />
                  </div>
                  <div className="col-span-4 space-y-1">
                    <Label className="text-[10px] uppercase font-bold text-muted-foreground">Amount (BDT)</Label>
                    <Input 
                      type="number" 
                      {...register(`bills.${index}.billAmount` as const)} 
                      required 
                      onChange={(e) => handleAmountChange(index, e.target.value)}
                    />
                  </div>
                  <div className="col-span-1 flex justify-end">
                    <Button type="button" variant="ghost" size="icon" className="text-destructive h-9 w-9" onClick={() => remove(index)}>
                      <Trash2 className="w-4 h-4" />
                    </Button>
                  </div>
                </div>
              ))}
            </div>

            {/* Validation Indicator */}
            {!ignoreTotalCheck && (
              <div className={cn(
                "flex items-center justify-between p-3 rounded-lg border",
                isPercentValid ? "bg-success/5 border-success/20 text-success" : "bg-warning/5 border-warning/20 text-warning"
              )}>
                <div className="flex items-center gap-2 text-sm font-medium">
                  {isPercentValid ? <CheckCircle2 className="w-4 h-4" /> : <AlertTriangle className="w-4 h-4" />}
                  Total Allocation: {totalPercentage.toFixed(2)}%
                </div>
                {!isPercentValid && <span className="text-xs">Must equal 100%</span>}
              </div>
            )}
          </div>

          <Button type="submit" className="w-full h-11" disabled={loading || (!isPercentValid && !ignoreTotalCheck)}>
            {loading ? <Loader2 className="w-4 h-4 animate-spin mr-2" /> : "Create Project & Initialize Billing"}
          </Button>
        </form>
      </DialogContent>
    </Dialog>
  );
}