"use client";

import { useState, useMemo, useEffect } from 'react';
import { ArrowUpDown, Settings2, Check } from 'lucide-react';
import { format } from 'date-fns';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { Checkbox } from '@/components/ui/checkbox';

export function ProjectTable({ projects, onProjectClick }: any) {
  // LocalStorage Column Visibility
  const [visibleColumns, setVisibleColumns] = useState({
    client: true,
    date: true,
    progress: true,
    status: true,
  });

  useEffect(() => {
    const saved = localStorage.getItem('project_table_cols');
    if (saved) setVisibleColumns(JSON.parse(saved));
  }, []);

  const toggleCol = (col: string) => {
    const updated = { ...visibleColumns, [col]: !visibleColumns[col] };
    setVisibleColumns(updated);
    localStorage.setItem('project_table_cols', JSON.stringify(updated));
  };

  const getInitials = (name: string) => name.split(' ').map(n => n[0]).join('').toUpperCase();

  const formatCurrency = (val: number) => 
    new Intl.NumberFormat('en-BD', { style: 'currency', currency: 'BDT', notation: 'compact' }).format(val);

  return (
    <div className="glass-card rounded-xl overflow-hidden border border-border/50">
      <div className="flex justify-end p-2 border-b bg-muted/20">
        <Popover>
          <PopoverTrigger asChild>
            <Button variant="ghost" size="sm" className="h-8 gap-2 text-xs"><Settings2 size={14}/> Column Settings</Button>
          </PopoverTrigger>
          <PopoverContent className="w-48 p-2">
            <p className="text-xs font-bold mb-2 px-2 uppercase text-muted-foreground">Show Columns</p>
            {Object.keys(visibleColumns).map(col => (
              <div key={col} className="flex items-center space-x-2 p-1">
                <Checkbox id={col} checked={visibleColumns[col]} onCheckedChange={() => toggleCol(col)} />
                <label htmlFor={col} className="text-sm capitalize cursor-pointer">{col}</label>
              </div>
            ))}
          </PopoverContent>
        </Popover>
      </div>

      <div className="overflow-x-auto">
        <table className="w-full text-left">
          <thead className="bg-muted/30 text-xs uppercase text-muted-foreground font-bold">
            <tr>
              <th className="p-4">Project</th>
              {visibleColumns.client && <th className="p-4">Client</th>}
              {visibleColumns.date && <th className="p-4">Sign Date</th>}
              <th className="p-4">Budget / Recovery</th>
              {visibleColumns.progress && <th className="p-4">Progress</th>}
              {visibleColumns.status && <th className="p-4">Status</th>}
              <th className="p-4 text-right">Actions</th>
            </tr>
          </thead>
          <tbody className="text-sm">
            {projects.map((p: any) => {
              const total = Number(p.totalProjectValue || 0);
              const rec = p.bills?.reduce((s: number, b: any) => s + Number(b.receivedAmount || 0), 0);
              const recPct = ((rec/total)*100).toFixed(0);
              const duePct = (100 - Number(recPct)).toFixed(0);

              return (
                <tr key={p.id} className="border-b border-border/50 hover:bg-muted/10 cursor-pointer transition-colors" onClick={() => onProjectClick(p)}>
                  <td className="p-4">
                    <p className="font-bold text-foreground">{p.projectName}</p>
                    <div className="flex gap-1 mt-1">
                      <Badge variant="outline" className="text-[9px] h-4 px-1 bg-primary/5">{getInitials(p.department?.name || '??')}</Badge>
                      <Badge variant="outline" className="text-[9px] h-4 px-1">{p.category?.name}</Badge>
                    </div>
                  </td>
                  {visibleColumns.client && <td className="p-4 text-muted-foreground">{p.client?.name}</td>}
                  {visibleColumns.date && <td className="p-4 text-muted-foreground">{p.startDate ? format(new Date(p.startDate), 'MMM d, yy') : 'N/A'}</td>}
                  <td className="p-4">
                    <p className="font-bold">{formatCurrency(total)}</p>
                    <div className="flex gap-2 text-[10px] font-bold mt-0.5">
                      <span className="text-success">REC: {recPct}%</span>
                      <span className="text-destructive">DUE: {duePct}%</span>
                    </div>
                  </td>
                  {visibleColumns.progress && <td className="p-4"><div className="w-16 h-1.5 bg-muted rounded-full overflow-hidden"><div className="h-full bg-success" style={{width: `${recPct}%`}}/></div></td>}
                  {visibleColumns.status && <td className="p-4"><Badge variant="secondary" className="text-[10px]">{p.status || 'Active'}</Badge></td>}
                  <td className="p-4 text-right"><Button variant="ghost" size="sm">Details</Button></td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </div>
  );
}